<template>
  <v-container>
    <v-data-table
        :headers="headers"
        :items="dataAirplane"
        :search="search"
        sort-by="Tipo"
        class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col
              cols="8"
              sm="4"
              md="2"
          >
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
            ></v-text-field>
          </v-col>

          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="900px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                Añadir
              </v-btn>
            </template>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>

                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="registration"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.registration"
                                :error-messages="errors"
                                label="Registro"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="totalHours"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.totalHours"
                                :error-messages="errors"
                                label="Total horas"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="certificate"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.certificate"
                                :error-messages="errors"
                                label="Certificado"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="executive"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.executive"
                                :error-messages="errors"
                                label="Exclusivo"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectLocation"
                              rules="required"
                          >
                            <v-select
                                v-model="selectLocation"
                                :items="dataLocation"
                                :error-messages="errors"
                                label="Selecionar ubicación"
                                item-text="location"
                                item-value="id"
                                data-vv-name="selectLocation"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectAirplaneType"
                              rules="required"
                          >
                            <v-select
                                v-model="selectAirplaneType"
                                :items="dataAirPlaneType"
                                :error-messages="errors"
                                label="Selecionar Tipo de Avión"
                                item-text="description"
                                item-value="id"
                                data-vv-name="selectAirplaneType"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectSure"
                              rules="required"
                          >
                            <v-select
                                v-model="selectSure"
                                :items="dataSure"
                                :error-messages="errors"
                                label="Selecionar Seguro"
                                item-text="id"
                                item-value="id"
                                data-vv-name="selectSure"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectMaker"
                              rules="required"
                          >
                            <v-select
                                v-model="selectMaker"
                                :items="dataMaker"
                                :error-messages="errors"
                                label="Selecionar Fabricante"
                                item-text="businessName"
                                item-value="id"
                                data-vv-name="selectMaker"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>


                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="cycles_ext"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.cycles_ext"
                                :error-messages="errors"
                                label="Ciclos extras"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="hor_pro_ins"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.hor_pro_ins"
                                :error-messages="errors"
                                label="hor_pro_ins"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="state"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.state"
                                :error-messages="errors"
                                label="Estado"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="hor_ult_insp"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.hor_ult_insp"
                                :error-messages="errors"
                                label="hor_ult_insp"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="totalCycles"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.totalCycles"
                                :error-messages="errors"
                                label="totalCycles"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="hours_ext"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.hours_ext"
                                :error-messages="errors"
                                label="hours_ext"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="3"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="por_ant_pi"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.por_ant_pi"
                                :error-messages="errors"
                                label="por_ant_pi"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>

                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                        class="mr-4"
                        type="submit"
                        color="#002F6C"
                        style="color: #E5F0F4;"
                        :disabled="invalid"
                    >
                      Guardar
                    </v-btn>
                  </v-card-actions>
                  <br/>

                </v-card>
              </form>
            </validation-observer>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">¿Está seguro que desea eliminar?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>

</template>

<script>
import { required,  max,  } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
setInteractionMode('eager')

extend('required', {
  ...required,
  message: ' Campo no puede ir vacío',
})

extend('max', {
  ...max,
  message: ' No puede ser mayor que {length} caracteres',
})



export default {
  name:'TableAirplane',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  props: ['dataAirplane', 'dataMaker', 'dataLocation', 'dataAirPlaneType', 'dataSure' ],
  data: () => ({
    search: '',
    checkbox: null,
    dialog: false,
    dialogDelete: false,
    updateAirplane: false,
    select: null,
    selectLocation: null,
    selectAirplaneType: null,
    selectMaker: null,
    selectSure: null,
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { text: 'Registro', value: 'registration' },
      { text: 'Total horas', value: 'totalHours' },
      { text: 'Horas extras', value: 'hours_ext' },
      { text: 'Localización', value: 'location.location' },
      { text: 'Certificado', value: 'certificate' },
      { text: 'Exclusivo', value: 'executive' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem:   {
      ability: "string",
      after_ext: 0,
      airPlaneType: {
        id: 1
      },
      alertCycles: 0,
      alertHours: 0,
      angarDate: "2022-06-05T15:31:25.682Z",
      ate_pro_ins: 0,
      aterAlerta: 0,
      certificate: 0,
      cic_pro_ins: 0,
      clase: 0,
      createdAt: "2022-06-05",
      createdBy: "string",
      cycles_ext: 0,
      date_fec_pro_ins: "2022-06-05T15:31:25.682Z",
      executive: "string",
      ext_ins: 0,
      fec_ult_ins: "2022-06-05T15:31:25.682Z",
      globalLandings: 0,
      hor_pro_ins: 0,
      hor_ult_cycle: 0,
      hor_ult_insp: 0,
      hours_ext: 0,
      id: 0,
      ins_avi_id_pro: 0,
      ins_avi_id_utl: 0,
      insuranceCarrierId: 0,
      leaseDate: 0,
      location: {
        id: 1
      },
      manufacturer: {
        id: 1
      },
      newTime: 0,
      pointId: 0,
      por_ant_pi: 0,
      productionDate: 0,
      registration: "string",
      royaLandings: 0,
      state: 0,
      sure: {
        id: 1
      },
      timeOverhaul: 0,
      totalCycles: 0,
      totalHours: 0,
      unit: 0,
      updatedAt: "2022-06-05",
      updatedBy: "string",
      usefulLifeHours: 0,
      usefulLifeTime: 0,
      usefulLifeUnit: 0,
      userAirPlane: 0,
      yearPurchase: 0
    },
    defaultItem: {
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo Airplane' : 'Editar Airplane'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  methods: {
    submit () {
      if (this.$refs.observer.validate()) {
        this.editedItem.location.id = this.selectLocation
        this.editedItem.airPlaneType.id = this.selectAirplaneType
        this.editedItem.sure.id = this.selectSure
        this.editedItem.manufacturer.id = this.selectMaker
        this.save()
      } else {
        console.log('completar los campos')
      }

    },

    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.selectLocation = this.editedItem.location.id
      this.selectAirplaneType = this.editedItem.airPlaneType.id
      this.editedItem.sure.id = this.selectSure
      this.editedItem.manufacturer.id = this.selectMaker
      this.updateAirplane = true;
      this.dialog = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.$emit('DeleteAirplane', this.editedItem.id)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if(this.updateAirplane) {
        this.$emit('updateAirplane', this.editedItem)
      } else {
        this.$emit('addAirplane', this.editedItem)
      }

      this.updateAirplane = false
      this.close()
    },
  },
}
</script>
<style scoped>

</style>
