<template>
  <div>
    <div className="container">
      <h1>Aeronaves</h1>
    </div>
    <TableAirplaneComponent
        @addAirplane="addAirplane"
        @updateAirplane="updateAirplane"
        @DeleteAirplane="DeleteAirplane"
        :dataAirplane="airplaneListState"
        :dataLocation="locationListState"
        :dataAirPlaneType="airPlaneTypeListState"
        :dataSure="sureListState"
        :dataMaker="makerListState"

    />
  </div>

</template>

<script>
import TableAirplaneComponent from "@/components/inventaryManagement/airplane/TableAirplaneComponent";
import {mapActions, mapGetters} from 'vuex'

export default {
  name: "AirplaneComponent",
  components: {
    TableAirplaneComponent
  },
  props: ['inventaryDataItem'],
  data() {
    return {
      dataAirplane: [],
      jsonDataUpdate:
          {
            ability: "string",
            after_ext: 0,
            airPlaneType: {
              id: 1
            },
            alertCycles: 0,
            alertHours: 0,
            angarDate: "2022-06-05T15:31:25.682Z",
            ate_pro_ins: 0,
            aterAlerta: 0,
            certificate: 0,
            cic_pro_ins: 0,
            clase: 0,
            createdAt: "2022-06-05",
            createdBy: "string",
            cycles_ext: 0,
            date_fec_pro_ins: "2022-06-05T15:31:25.682Z",
            executive: "string",
            ext_ins: 0,
            fec_ult_ins: "2022-06-05T15:31:25.682Z",
            globalLandings: 0,
            hor_pro_ins: 0,
            hor_ult_cycle: 0,
            hor_ult_insp: 0,
            hours_ext: 0,
            id: 0,
            ins_avi_id_pro: 0,
            ins_avi_id_utl: 0,
            insuranceCarrierId: 0,
            leaseDate: 0,
            location: {
              id: 1
            },
            manufacturer: {
              id: 1
            },
            newTime: 0,
            pointId: 0,
            por_ant_pi: 0,
            productionDate: 0,
            registration: "string",
            royaLandings: 0,
            state: 0,
            sure: {
              id: 1
            },
            timeOverhaul: 0,
            totalCycles: 0,
            totalHours: 0,
            unit: 0,
            updatedAt: "2022-06-05",
            updatedBy: "string",
            usefulLifeHours: 0,
            usefulLifeTime: 0,
            usefulLifeUnit: 0,
            userAirPlane: 0,
            yearPurchase: 0
          }
    }
  },
  mounted() {
    this.updateData()
    this.getLocationState()
    this.getAirPlaneTypeState()
    this.getSureState()
    this.getMakerState()
  },
  computed: {
    ...mapGetters(['sureListState','makerListState','airPlaneTypeListState', 'airplaneListState', 'locationListState', 'AirplaneTypeListState', 'locationListState',])
  },
  methods: {
    ...mapActions(['getAirPlaneState', 'getMakerState', 'getSureState', 'getLocationState', 'setAirPlaneStatenew', 'getAirPlaneTypeState', 'apiUpdateAirplane', 'getLocationState', 'apiDeleteAirplane',]),

    async addAirplane(item) {
      await this.setAirPlaneStatenew(item)
      await this.updateData()
    },
    async updateAirplane(item) {
      this.jsonDataUpdate.createdBy = item.createdBy;
      this.jsonDataUpdate.registration = item.registration;
      this.jsonDataUpdate.newTime = item.newTime;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.certificate = item.certificate;
      this.jsonDataUpdate.totalHours = item.totalHours;
      this.jsonDataUpdate.totalCycles = item.totalCycles;
      this.jsonDataUpdate.hor_ult_insp = item.hor_ult_insp;
      this.jsonDataUpdate.hor_ult_cycle = item.hor_ult_cycle;
      this.jsonDataUpdate.fec_ult_ins = item.fec_ult_ins;
      this.jsonDataUpdate.airPlaneType.id = item.airPlaneType.id;
      this.jsonDataUpdate.sure.id = item.sure.id;
      this.jsonDataUpdate.manufacturer.id = item.manufacturer.id;

      this.jsonDataUpdate.hours_ext = item.hours_ext;
      this.jsonDataUpdate.royaLandings = item.royaLandings;
      this.jsonDataUpdate.executive = item.executive;
      this.jsonDataUpdate.ability = item.ability;
      this.jsonDataUpdate.userAirPlane = item.userAirPlane;
      this.jsonDataUpdate.usefulLifeHours = item.usefulLifeHours;
      this.jsonDataUpdate.cycles_ext = item.cycles_ext;
      this.jsonDataUpdate.hor_pro_ins = item.hor_pro_ins;
      this.jsonDataUpdate.state = item.state;
      this.jsonDataUpdate.date_fec_pro_ins = item.date_fec_pro_ins;
      await   this.apiUpdateAirplane(this.jsonDataUpdate)
      await this.updateData()
    },
    async updateData() {
      await this.getAirPlaneState()
    },
    async  DeleteAirplane(item) {
      await this.apiDeleteAirplane(item)
      await this.updateData()
    }
  }
}
</script>

